<template>
  <!-- height="100vh" -->
  <v-app>
    <!-- <q-banner
      dense
      class="bg-primary text-white"
      v-if="isMobileCheck == true && appWasInstled == false"
    >
      install App ?
      <template v-slot:action>
        <q-btn flat color="white" label="Dismiss" />
        <q-btn color="green" dark label="yes" @click="installApp" />
      </template>
    </q-banner> -->

    <v-alert
      prominent
      type="info"
      v-if="isMobileCheck == true && appWasInstled == false"
    >
      <v-row align="center col-12">
        <v-col class="grow"> install App ? </v-col>
        <v-col class="shrink col-12">
          <v-btn color="green" @click="installApp">yes</v-btn>
          <v-btn color="red" @click="installApp">no</v-btn>
        </v-col>
      </v-row>
    </v-alert>
    <!-- menu -->
    <v-layout row>
      <v-flex md12>
        <v-card class="mt-3">
          <TopMenu @drawer="drawerFn" />
        </v-card>
      </v-flex>
    </v-layout>
    <!-- carousel -->
    <v-layout row>
      <v-flex md12>
        <router-view></router-view>
        <!-- <Carousel /> -->
      </v-flex>
    </v-layout>
    <!-- drawer -->
    <v-navigation-drawer v-model="drawer" absolute temporary height="100vh">
      <v-list>
        <v-list-item>
          <v-list-item-icon>
            <v-icon>mdi-home</v-icon>
          </v-list-item-icon>

          <v-list-item-title>Home</v-list-item-title>
        </v-list-item>

        <v-list-group :value="true" prepend-icon="mdi-account-circle">
          <template v-slot:activator>
            <v-list-item-title>Users</v-list-item-title>
          </template>

          <v-list-group :value="true" no-action sub-group>
            <template v-slot:activator>
              <v-list-item-content>
                <v-list-item-title>Admin</v-list-item-title>
              </v-list-item-content>
            </template>

            <v-list-item v-for="([title, icon], i) in admins" :key="i" link>
              <v-list-item-title v-text="title"></v-list-item-title>

              <v-list-item-icon>
                <v-icon v-text="icon"></v-icon>
              </v-list-item-icon>
            </v-list-item>
          </v-list-group>

          <v-list-group no-action sub-group>
            <template v-slot:activator>
              <v-list-item-content>
                <v-list-item-title>Actions</v-list-item-title>
              </v-list-item-content>
            </template>

            <v-list-item v-for="([title, icon], i) in cruds" :key="i" link>
              <v-list-item-title v-text="title"></v-list-item-title>

              <v-list-item-icon>
                <v-icon v-text="icon"></v-icon>
              </v-list-item-icon>
            </v-list-item>
          </v-list-group>
        </v-list-group>
      </v-list>
    </v-navigation-drawer>
  </v-app>
</template>

<script>
// import Carousel from "./components/Carousel.vue";
import TopMenu from "./components/TopMenu.vue";
import { mapGetters } from "vuex";
export default {
  name: "App",
  components: {
    // Carousel,
    TopMenu,
  },
  methods: {
    drawerFn() {
      this.drawer = true;
    },
    installApp() {
      this.appWasInstled = true;

      this.deferredPrompt.prompt();
      // Wait for the user to respond to the prompt
      this.deferredPrompt.userChoice.then((choiceResult) => {
        if (choiceResult.outcome === "accepted") {
          this.appWasInstled = true;
          localStorage.setItem("appWasInstled", true);
          this.showInstall = false;
          console.log("User accepted the A2HS prompt 1");
        } else {
          this.showInstall = false;
          console.log("User dismissed the A2HS prompt 2");
        }
        console.log("User accepted the A2HS prompt 3");
        this.deferredPrompt = null;
        // this.defthis.erredPrompt = null;
      });
    },
  },
  computed: {
    ...mapGetters(["shopData"]),
  },
  data: () => ({
    //
    leftDrawerOpen: false,
    deferredPrompt: null,
    appWasInstled: false,

    drawer: false,
    group: null,
    items: [
      { title: "Click Me" },
      { title: "Click Me" },
      { title: "Click Me" },
      { title: "Click Me 2" },
    ],
    admins: [
      ["Management", "mdi-account-multiple-outline"],
      ["Settings", "mdi-cog-outline"],
    ],
    cruds: [
      ["Create", "mdi-plus-outline"],
      ["Read", "mdi-file-outline"],
      ["Update", "mdi-update"],
      ["Delete", "mdi-delete"],
    ],
  }),

  mounted() {
    // debugger;
    let appWasInstled = localStorage.getItem("appWasInstled");
    if (appWasInstled) this.appWasInstled = true;
    window.addEventListener("beforeinstallprompt", (e) => {
      console.log("Ubeforeinstallprompt");

      e.preventDefault();
      this.deferredPrompt = e;
    });
  },
};
</script>
